import Component from 'react'
import Link from 'next/link'
import Near from 'nearlib'

const accountId = "alice.near";
const contractName = "metanear";

class Game extends React.Component {
    constructor(props) {
        super(props)
        this.state = {cells: []}
    }
    componentDidMount() {
        console.log(Near);
        Near.Near.loadContract(contractName, {
            viewMethods: ["getCellView"],
            changeMethods: [],
            sender: accountId
        }).then((contract) => {
            this.contract = contract;
            this.contract.getCellView()
                .then((cells) => {
                    this.setState({ cells })
                });
        })
    }
    render() {
        return (
            <div>
                {this.state.cells}
            </div>
        )
    }
}

export default function Index() {
    return (
        <div>
            <Game />
        </div>
    )
}